@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
:root {
  --font-family: 'Manrope', sans-serif;

  --gradient-text: linear-gradient(90deg, rgba(238, 130, 238, 1) 0%, rgba(0, 209, 255, 1) 100%);
  --gradient-bar: linear-gradient(90deg, rgba(238, 130, 238, 1) 0%, rgba(0, 209, 255, 1) 100%);
  
  --color-bg: #040C18;
  --color-text: #81AFDD;
  --color-subtext: #FF8A71;
  
}

.gradient__text {
  background: var(--gradient-text);
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-background-clop: text;
  -webkit-text-fill-color: transparent;
}


body {
  padding: 0;
  margin: 0;
  background: var(--color-bg);
  color: #FFF;
  font-family: var(--font-family);
}

body::-webkit-scrollbar {
  width:10px;
  height: 10px;
}

body::-webkit-scrollbar-track {
  background:#333;
}

body::-webkit-scrollbar-thumb {
  background: #43065c;
  border-radius: 4px;
}

.container {
  display: flex;
  justify-items: center;
  flex-wrap: wrap;
  max-width: 600px;
  margin: 0 auto;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  padding: 5px;
  margin-bottom: 0.5rem;
}

.container img {
  margin: 20px;
  padding: 20px;
  height: 30%;
  width: 30%;
  border-radius: 50%;
  margin-top: 20px;
  margin-bottom: 0.1rem;
  align-items: center;
  text-align: center;
}

.text {
  font-size: 25px;
  text-align: center;
  align-items: center;
  margin-top: 90px;
}

.social {
  margin: 20px;
  padding: 30px;
  height: 80px;
  width: 50vh;
  text-align: center;
  cursor: pointer;
}

.social :hover{
  transform:translate(1) ;
}

a {
  text-decoration: none;
  color: white;
}

a:visited {
  text-decoration: none;
}

#linkedin {
  background: rgb(19, 60, 122);
  text-align: center;
  border-radius: 4px;  
  
}

#portfolio {
  background:#bc1888;
  text-align: center;
  border-radius: 4px;
}

#instagram {
  top: 1px;
  background: linear-gradient( 45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100% );
  text-align: center;
  border-radius: 4px;
}

#github {
  background: rgb(46, 45, 45);
  text-align: center;
  border-radius: 4px;
}

#icon {
  margin-bottom: -3px;
}
